import {Injectable} from '@angular/core';
import {SearchInterface} from "../../declarations";

@Injectable({providedIn: 'root'})
export class AppleMapsSearchService {
    public searchObject: any;

    constructor() {
    }

    public initSearch(options = {}) {
        this.searchObject = new window.mapkit.Search(options);
    }

    public search(query: any, callback: (err: any, data?: any) => any, options: SearchInterface) {
        if (!this.searchObject) {
            throw new Error('Search or API don\'t initialized');
        }
        this.searchObject.search(query, (err: any, data: any) => {
            if (err) {
                callback(err);
                return;
            }
            callback(err, data);
        }, options);
    }

    public autocomplete(query: any, callback: (err: any, data?: any) => any, options: SearchInterface) {
        if (!this.searchObject) {
            throw new Error('Search don\'t initialized');
        }
        this.searchObject.autocomplete(query, (err: any, data: any) => {
            if (err) {
                callback(err);
                return;
            }
            callback(err, data);
        }, options);
    }
}
