import {
    AfterViewInit,
    ChangeDetectorRef,
    Component,
    ContentChildren,
    DoCheck,
    EventEmitter,
    Input,
    KeyValueChanges,
    KeyValueDiffers,
    OnInit,
    Output,
    QueryList,
    ViewContainerRef
} from '@angular/core';
import {MapConstructorOptions, MapKitInitOptions} from "../../declarations";
import {BehaviorSubject} from "rxjs";
import {AppleMapsService} from "../../apple-maps.service";
import {NgxAppleMapkitAnnotationComponent} from "../ngx-apple-mapkit-annotation/ngx-apple-mapkit-annotation.component";

@Component({
    selector: 'ngx-apple-mapkit',
    templateUrl: './ngx-apple-mapkit.component.html',
    styleUrls: ['./ngx-apple-mapkit.component.css']
})
export class NgxAppleMapkitComponent implements DoCheck, OnInit, AfterViewInit {
    @Input() options: MapKitInitOptions;
    @Input() settings: MapConstructorOptions;
    @Input() height: string;
    @Output() onLoaded = new EventEmitter();
    @ContentChildren('ngx-apple-mapkit-annotation', {descendants: true}) annotations: QueryList<NgxAppleMapkitAnnotationComponent>;
    // @ContentChildren(TemplateRef, {descendants: true}) template: QueryList<TemplateRef<any>>;
    // private templates: QueryList<TemplateRef<any>>;
    public defaultOptions = {language: 'en'};
    // annotations = [];
    public defaultSettings = {
        colorScheme: window.matchMedia && window.matchMedia('(prefers-color-scheme: dark)').matches ? 'dark' : 'light',
        isZoomEnabled: true,
        mapType: 'standard',
        showsZoomControl: true,
        showsMapTypeControl: true
    };
    // tslint:disable-next-line:variable-name
    private _options: any;
    // tslint:disable-next-line:variable-name
    private _settings: any;
    private keyValue: number;
    public keySource = new BehaviorSubject<number>(-1);
    public key = this.keySource.asObservable();

    constructor(private differs: KeyValueDiffers, private appleMapsService: AppleMapsService, private viewTemplateRef: ViewContainerRef, private cdr: ChangeDetectorRef) {
        this.settings = {};
    }

    private init() {
        const settings = Object.assign({...this.defaultSettings, ...this.settings});
        const options = Object.assign({...this.defaultOptions, ...this.options});
        this.appleMapsService.init(options, settings, (data) => {
            if (this.onLoaded.length) {
                this.onLoaded.emit(data);
            }
            this.keyValue = data.key;
            this.keySource.next(data.key);
        });
    }

    private optionsChanged(changes: KeyValueChanges<string, any>) {
        this.appleMapsService.optionsChanged(changes);
    }

    private settingsChanged(changes: KeyValueChanges<string, any>) {
        this.appleMapsService.settingsChanged(changes, this.keyValue);
    }

    // ngAfterContentInit(): void {
    //     this.template.forEach(item => {
    //         const template = this.viewTemplateRef.createEmbeddedView(item);
    //         this.viewTemplateRef.remove(this.viewTemplateRef.indexOf(template));
    //         template.detectChanges();
    //         this.annotations.push(item);
    //         this.viewTemplateRef.clear();
    //     });
    // }

    ngOnInit() {
        if (!this.options) {
            throw new Error('You must provide JWT token');
        } else {
            this._options = this.differs.find(this.options).create();
            this.init();
        }
        if (this.settings) {
            this._settings = this.differs.find(this.settings).create();
        }
    }

    ngDoCheck() {
        if (this.options) {
            const options = this._options.diff(this.options);
            if (options) {
                this.optionsChanged(options);
            }
        }
        if (this.settings) {
            const settings = this._settings.diff(this.settings);
            if (settings) {
                this.settingsChanged(settings);
            }
        }
    }

    ngAfterViewInit(): void {
        this.cdr.detectChanges();
    }
}
