import {Injectable} from '@angular/core';
import {GeocoderConstructorOptionsInterface, GeocoderReverseLookupOptionsInterface} from '../../declarations';

@Injectable({providedIn: 'root'})
export class AppleMapsGeocoderService {
    private geocoder: any;

    constructor() {
    }

    public initGeocoder(options: GeocoderConstructorOptionsInterface = {}) {
        this.geocoder = new window.mapkit.Geocoder(options);
    }

    public reverseLookup(lat: number, lon: number, callback: (err: any, data?: any) => any, options: GeocoderReverseLookupOptionsInterface = {}) {
        if (!this.geocoder) {
            this.initGeocoder();
        }
        this.geocoder.reverseLookup(new window.mapkit.Coordinate(lat, lon), (err, result) => {
            if (err) {
                callback(err, null);
            } else {
                callback(null, result);
            }
        }, options);
    }
}
